import React from "react";

const Main = () => {
  function fetchContent() {
    const result = document.getElementById("result");
    let userName = document.getElementById("search").value;
    let url = "https://api.github.com/users/" + userName;
    console.log(url);
    // console.log(userName);
    fetch(url)
      .then((res) => res.json())
      .then((data) => {
        result.innerHTML = `<img id="user_image" src=${data.avatar_url}></br>
  <span>Welcome, I'm <span id="dataname">${data.name}</span></span></br>
   
   <p id="databio">${data.bio}</br></p>`;
      });
  }

  return (
    <>
      <div>
        <section>
          <h1>Just type your username and see the magic</h1>
          <input
            id="search"
            type="text"
            placeholder="Github Username"
            autoComplete="off"
          />
          <br />
          <br />
        </section>

        <button onClick={fetchContent} className="btn" id="myBtn">
          Get Github CV
        </button>

        <p id="userContent"></p>
        <p id="result"></p>

        <big>
          Developed by<a href="https://vikas-singh.netlify.com/">Vikas Singh</a>
        </big>
      </div>
    </>
  );
};

export default Main;
